import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Categoria_ocorrencia } from '../../../modelo/Categoria_ocorrencia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'categoria_ocorrencia-dados-details',   templateUrl: 'categoria_ocorrencia_dadosDetails.component.html' }) export class Categoria_ocorrenciaDadosDetailsComponent implements OnInit {    categoria_ocorrencia: Categoria_ocorrencia;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.categoria_ocorrencia.Categoria_ocorrencia";                 let id = +params['id'];         this.categoria_ocorrencia = new Categoria_ocorrencia();         this.categoria_ocorrencia.cd_categoria_ocorrencia = id;         o.objetoJson = this.categoria_ocorrencia;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a categoria_ocorrencia. '; }      this.categoria_ocorrencia = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


